import { Component, OnInit } from '@angular/core';
import { EncryptDecryptLocalStorageService } from '../services/encrypt-decrypt-localstorage.service';
import { UrlConstant } from '../services/urlConstant';
import { Genericservice } from '../services/genericservices';
import { Router } from '@angular/router';
@Component({
  selector: 'app-admin-home',
  templateUrl: './admin-home.component.html',
  styleUrls: ['./admin-home.component.scss']
})
export class AdminHomeComponent implements OnInit {
  notificationList: any = [];
  employeeId: any;
  isNotification: boolean;
  firstName: any;
  lastName: any;
  name: any;
  public isImage: boolean = false;
  adminDashBoardName: any;
  constructor(private gernericservice: Genericservice,
    private encryptdecryptlocalstorage: EncryptDecryptLocalStorageService,
    private router:Router) { }

  ngOnInit() {
    this.employeeId = this.encryptdecryptlocalstorage.getLocalStorage('employeeId');
    this.isNotification = false;
      this.getNotificationList();
      this.notification();
  }
  getNotificationList() {
    return this.gernericservice.get(UrlConstant.getNotificationAPI + '?employeeId='+this.employeeId).subscribe(res => {
      this.notificationList = res.data;
    })
  }
  notification() {
    this.employeeId = this.encryptdecryptlocalstorage.getLocalStorage('employeeId');
    this.isNotification = false;
    return this.gernericservice.get(UrlConstant.notificationAPI + "?employeeId=" + this.employeeId + "&isNotification=" + this.isNotification).subscribe(response => {
       this.adminDashBoardName = response.data.firstName
      if (response.data.profileImage != null && response.data.profileImage != undefined) {
        this.isImage = true;
        this.name = response.data.profileImage;
      } else {
        this.isImage = false;
        this.firstName = response.data.firstName.charAt(0);
        this.lastName = response.data.lastName.charAt(0);
        this.name = this.firstName + this.lastName;
      }
    }
    )
  }

  logout(){
    localStorage.clear();
    sessionStorage.clear();
    this.router.navigate(['/signIn']);
  }
  

}
